import { useState, useRef, useCallback, useEffect } from "react";
import { message } from "antd";
import {
  allDataWallets,
  getAllFilterValue,
  walletsREports,
} from "../api/login/Login";

const getCurrentDate = () => {
  const today = new Date();
  return today.toISOString().substr(0, 10);
};

const useWalletReport = () => {
  const storedUserType = localStorage.getItem("userType");
  const [currentDate, setCurrentDate] = useState(getCurrentDate());
  const [allFilter, setAllFilter] = useState([]);
  const [loading, setLoading] = useState(false);
  const [count, setCount] = useState(10);
  const [page, setPage] = useState(0);
  const [totalCount, setTotalCount] = useState(null);
  const [transType, setTransType] = useState();
  const [data, setData] = useState([]);
  const [allData, setAllData] = useState(null);
  const [excelData, setExcelData] = useState(null);
  const [sortDirection, setSortDirection] = useState();
  const [assending, setDecending] = useState(1);

  const currentCount = useRef(10);
  const [transTypeMap, setTransTypeMap] = useState({});

  const [filterInitial, setFilterInitial] = useState({
    user_id: "",
    count: "",
    page: "",
    min_amt: 0,
    max_amt: 0,
    end_date: "",
    start_date: "",
    type: "",
    trans_type: "",
    order_id: "",
    txn_id: "",
    sortType: "",
    sortKey: "",
  });

  const handleChange = (e) => {
    const clone = { ...filterInitial };
    const value = e.target.value;
    const name = e.target.name;
    clone[name] = value;
    setFilterInitial(clone);
  };

  const getTransitionReport = useCallback(
    async (input, newCount) => {
      setLoading(true);
      const useCount = newCount !== undefined ? newCount : currentCount.current;
      const clone = {
        ...filterInitial,
        count: useCount,
        page: input || 0,
        min_amt: +filterInitial.min_amt,
        max_amt: +filterInitial.max_amt,
        user_id: window.localStorage.getItem("userIdToken"),
      };
      const key = (filterInitial.trans_type || "").toLowerCase().trim();
      if (transTypeMap[key]) {
        clone.trans_type = transTypeMap[key];
      } else {
        clone.trans_type = filterInitial.trans_type || "";
      }

      console.log("FINAL PAYLOAD SENT:", clone);
      try {
        const res = await walletsREports(clone);
         if (res?.data?.data?.wallet?.length > 0) {
        const map = {};
        res.data.data.wallet.forEach((item) => {
          if (item.trans_type) {
            map[item.trans_type.toLowerCase()] = item.trans_type;
          }
        });
        setTransTypeMap(map);
      }
        setTotalCount(res?.data?.data?.total);
        setData(res?.data?.data?.wallet || []);
      } catch (error) {
        console.error("Error fetching data:", error);
      }
      setLoading(false);
    },
    [filterInitial]
  );

  const onChangeVal = (e) => {
    setPage(e - 1);
    getTransitionReport(e - 1);
  };

  const handleSearch = () => {

  const start = filterInitial.start_date;
  const end = filterInitial.end_date;

  if (start && end) {
    const startDate = new Date(start);
    const endDate = new Date(end);

    if (startDate > endDate) {
      message.error("Wrong Date Combination");
      return;
    }
  }
  setPage(0);
  getTransitionReport(0);
};


  const ResetData = async () => {
    setLoading(true);
    const obj = {
      count: count || 10,
      page: 0,
      min_amt: 0,
      max_amt: 0,
      end_date: "",
      start_date: "",
      type: "",
      trans_type: "",
      order_id: "",
      txn_id: "",
      sortType: "",
      sortKey: "",
      user_id: window.localStorage.getItem("userIdToken"),
    };
    setTransType(null);

    try {
      const res = await walletsREports(obj);
      setTotalCount(res?.data?.data?.total);
      setData(res?.data?.data?.wallet || []);
      setFilterInitial({
        user_id: "",
        count: "",
        page: "",
        min_amt: 0,
        max_amt: 0,
        end_date: "",
        start_date: "",
        type: "",
        trans_type: "",
        order_id: "",
        txn_id: "",
        sortType: "",
        sortKey: "",
      });
      setPage(0);
    } catch (error) {
      console.error("Reset error:", error);
    }
    setLoading(false);
  };

  const sortByColumn = async (key) => {
    if (sortDirection == "asc") {
      setDecending(1);
    } else {
      setDecending(-1);
    }

    setSortDirection(sortDirection === "asc" ? "desc" : "asc");

    setLoading(true);

    const clone = {
      ...filterInitial,
      count: count,
      page: page,
      min_amt: +filterInitial.min_amt,
      max_amt: +filterInitial.max_amt,
      sortType: +assending,
      sortKey: key,
      user_id: window.localStorage.getItem("userIdToken"),
    };

    try {
      const res = await walletsREports(clone);
      setTotalCount(res?.data?.data?.total);
      setData(res?.data?.data?.wallet || []);
    } catch (error) {
      console.error("Sorting failed:", error);
    }
    setLoading(false);
  };

  const allDataWalletReport = async () => {
    const clone = {
      ...filterInitial,
      count: count,
      page: page,
      min_amt: +filterInitial.min_amt,
      max_amt: +filterInitial.max_amt,
      user_id: window.localStorage.getItem("userIdToken"),
    };
    try {
      const res = await allDataWallets(clone);
      setAllData(res?.data?.data?.wallet);
    } catch (error) {
      console.error("All data fetch error:", error);
    }
  };

  const handlePageSizeChange = useCallback(
    (value) => {
      currentCount.current = value;
      setCount(value);
      setPage(0);
      setFilterInitial((prev) => ({ ...prev, count: value, page: 0 }));
      getTransitionReport(0, value);
    },
    [getTransitionReport]
  );

  const prepareExcelDownload = async () => {
    setLoading(true);
    const clone = {
      ...filterInitial,
      count: count,
      page: page,
      min_amt: +filterInitial.min_amt,
      max_amt: +filterInitial.max_amt,
      user_id: window.localStorage.getItem("userIdToken"),
    };
    try {
      const res = await allDataWallets(clone);
      const data = res?.data?.data?.wallet;

      if (!data || data.length === 0) {
        message.error("No data available to export.");
        setLoading(false);
        return;
      }

      setExcelData(data);
      setTimeout(() => {
        document.getElementById("test-table-xls-button")?.click();
        setLoading(false);
        message.success("Excel file download initiated.");
      }, 100);
    } catch (error) {
      console.error("Excel export error:", error);
      message.error("Failed to prepare data for export.");
      setLoading(false);
    }
  };

  const getAllFilter = async () => {
    try {
      let res = await getAllFilterValue();
      setAllFilter(res?.data?.data);
    } catch (error) {
      console.error("Failed to fetch filter values:", error);
    }
  };

  useEffect(() => {
    getCurrentDate();
    getAllFilter();
  }, []);

  useEffect(() => {
    getTransitionReport(0);
  }, []);

  return {
    currentDate,
    storedUserType,
    allFilter,
    loading,
    count,
    page,
    totalCount,
    transType,
    data,
    allData,
    excelData,
    filterInitial,
    currentCount,

    setCurrentDate,
    setAllFilter,
    setLoading,
    setCount,
    setPage,
    setTotalCount,
    setTransType,
    setData,
    setAllData,
    setExcelData,
    setFilterInitial,
    setSortDirection,
    setDecending,
    handleChange,
    getTransitionReport,
    onChangeVal,
    handleSearch,
    ResetData,
    sortByColumn,
    allDataWalletReport,
    handlePageSizeChange,
    prepareExcelDownload,
    currentDate,
    storedUserType,
    transType,
    setFilterInitial,
    setTransType,
    allFilter,
    excelData,
  };
};

export default useWalletReport;
